@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
body {
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
@media (min-width: 320px) and (max-width: 767px) {
  .advantages-list {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .advantages-list__item {
    padding: 0;
    margin: 0;
    line-height: 0;
    width: 100%;
  }
  .advantages-list__item img {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }
  .advantages-list__container.advantages-list__container--color-container {
    padding: 37px 18px 37px 18px;
  }
  .advantages-list__item:first-child .advantages-list__container {
    background-image: url("../img/triangle.svg");
    background-size: auto;
    background-repeat: no-repeat;
    background-position: top;
    padding: 54px 18px 40px 18px;
  }
  .advantages-list__item:first-child .advantages-list__title {
    padding: 0;
  }
  .advantages-list__item:first-child .advantages-list__description {
    margin: 17px 0 0 0;
  }
  .advantages-list__container--color-container {
    background-color: #81b3d2;
    color: #ffffff;
  }
  .advantages-list:nth-child(2) .advantages-list__title {
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-transform: uppercase;
    margin: 0;
  }
  .advantages-list__container--color-container .advantages-list__title {
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-transform: uppercase;
    margin: 0;
    text-align: center;
  }
  .advantages-list__container--color-container .advantages-list__description-counter {
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    text-transform: uppercase;
    margin: 0;
    text-align: center;
    margin-top: 11px;
  }
  .advantages-list__container--color-container .advantages-list__description {
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    text-transform: uppercase;
    margin: 0;
    text-align: center;
    margin-top: 17px;
  }
  .advantages-list__item--accent {
    background-color: #EEEEEE;
    padding: 0 24px 0 24px;
  }
  .advantages-list__item--accent .advantages-list__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #d6d6d6;
    padding: 0 12px 0 12px;
  }
  .advantages-list__item--accent:last-child .advantages-list__container {
    border-bottom: none;
  }
  .advantages-list__item--accent .advantages-list__title {
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-transform: uppercase;
    margin: 0;
    text-align: center;
    margin-top: 24px;
  }
  .advantages-list__item--accent .advantages-list__description-counter {
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    text-transform: uppercase;
    margin: 0;
    text-align: center;
    margin-top: 11px;
  }
  .advantages-list__item--accent .advantages-list__description {
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    text-transform: uppercase;
    margin: 0;
    text-align: center;
    margin-bottom: 26px;
    margin-top: 16px;
  }
}
@media (min-width: 768px) {
  .advantages-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto auto auto;
    margin: 0;
    grid-auto-flow: row dense;
  }
  .advantages-list__item:first-child {
    grid-column: 1/4;
  }
  .advantages-list__item {
    list-style-type: none;
  }
  ul .services-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto;
    margin: 0;
    padding: 0;
  }
  .services-item {
