    display: grid;
    justify-content: space-between;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-auto-flow: row dense;
    grid-template-rows: 56px;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    text-transform: uppercase;
    margin: 0;
    text-decoration: none;
    padding: 0;
    justify-items: center;
    align-items: center;
  }
  .menu-list::before,
  .menu-list::after {
    content: "";
    grid-column: 3/4;
  }
  .menu-list::after {
    grid-row: 2/3;
  }
